* {
  box-sizing: border-box;
  list-style: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form button, .load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.load-more {
  margin: 10px auto 0;
}

.search-form button:hover, .load-more:hover {
  background-color: #0069d9;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

button.hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.photo-card {
  width: 300px;
  border: 1px solid #ccc;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: 200px;
  margin-bottom: 10px;
}

.photo-card .info {
  font-size: 14px;
}

.photo-card .info-item {
  margin-top: 2px;
  margin-bottom: 2px;
}

.photo-card .info-item b {
  font-weight: bold;
}

